.MuiBox-root.cardmodal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 40px;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid #000;
    background-color: #fff;
    box-shadow: 0px 11px 15px -7px rgba(0, 0, 0, 0.2), 0px 24px 38px 3px rgba(0, 0, 0, 0.14), 0px 9px 46px 8px rgba(0, 0, 0, 0.12);
    overflow-y: auto;
}

.cardmodal__closebtn {
    align-self: flex-end;
}

.cardmodal__body {
    display: flex;
    align-items: center;
}

.cardmodal__card-vid {
    max-width: 90%;
    height: auto;
}

.cardmodal__card {
    margin-right: 20px;
}

@media (max-width: 1000px) {
  
    .cardmodal__body {
        flex-direction: column;
        text-align: center;
    }

    .cardmodal__card {
        margin-right: 0;
    }

    .cardmodal__card-vid {
        width: 80%;
        height: auto;
    }
}

@media (max-height: 425px) {
    .cardmodal__body {
        flex-direction: row;
    }
}